.programs__block {
  max-width: 280px;
  min-height: 266px;
  margin: 20px;
  background-color: #D9D9D9;
}

.programs__item-title {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 37px;
  text-transform: uppercase;
  color: #000000;
  margin: 0;
}

.programs__item-text {
  font-family: "Lato", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}

.programs__text-block {
  display: flex;
  padding: 0 30px 0 20px;
}

.programs__item-block {
  display: flex;
  min-width: 180px;
  margin-left: 22px;
  padding-top: 30px;
}

.programs__link-block {
  display: flex;
  align-items: center;
  min-height: 24px;
  padding: 38px 0 0 20px;
}

.programs__item-link {
  font-family: "Oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
  text-decoration: none;
  display: flex;
  justify-content: flex-start;
  min-width: 134px;
}
